import React, { useRef, useState } from 'react';
import emailjs from '@emailjs/browser';
import { ContactProps } from '../../../types';
import PageNavigation from '../PageNavigation/PageNavigation';
import { getTranslations, interpolate } from '../../../i18n';

import border_dark from '../../../img/border_dark.svg';

import './Contact.css';

const Contact: React.FC<ContactProps> = ({ setDialogText, setCurrentAction, lang }) => {
  const t = getTranslations(lang);
  const [name, setName] = useState<string>('');
  const [email, setEmail] = useState<string>('');
  const [message, setMessage] = useState<string>('');
  const [sending, setSending] = useState<boolean>(false);
  const [sent, setSent] = useState<boolean>(false);
  const [error, setError] = useState<boolean>(false);
  const [errorMsg, setErrorMsg] = useState<string>('');

  const form = useRef<HTMLFormElement>(null);

  const sendEmail = (e: React.FormEvent) => {
    e.preventDefault();
    setSending(true);
    if (form.current) {
      emailjs
        .sendForm('service_q3stgbj', 'template_hb21y1x', form.current, 'ez1gnij5WiOdzyc5t')
        .then(
          (result) => {
            console.log(result.text);
            setSending(false);
            setSent(true);
          },
          (error) => {
            console.error(error.text);
            setSending(false);
            setError(true);
            setErrorMsg(error.text);
          }
        );
    }
  };

  const renderForm = () => {
    if (sending) {
      return (
        <div id="sending-mail">
          <i className="spinner icon"></i>
        </div>
      );
    }
    if (sent && !error && !sending) {
      return (
        <div className="ui positive message">
          <div className="header">{t.contact.successMessage}</div>
        </div>
      );
    } else {
      return (
        <>
          <p>{t.contact.subtitle}</p>
          {error && (
            <div className="ui negative message">
              <div className="header">{errorMsg}</div>
            </div>
          )}
          <form className="ui form" id="contact-form" ref={form} onSubmit={sendEmail}>
            <div className="contact-field field">
              <label style={{ fontSize: '12px', color: '#0000ff' }}>{t.contact.nameLabel}</label>
              <input
                id="name"
                name="name"
                type="text"
                placeholder={t.contact.namePlaceholder}
                value={name}
                onChange={(e) => {
                  setName(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              />
            </div>
            <div className="contact-field field">
              <label style={{ fontSize: '12px', color: '#0000ff' }}>{t.contact.emailLabel}</label>
              <input
                id="email"
                name="email"
                type="email"
                placeholder={t.contact.emailPlaceholder}
                value={email}
                onChange={(e) => {
                  setEmail(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              />
            </div>
            <div className="contact-field field">
              <label style={{ fontSize: '12px', color: '#0000ff' }}>{t.contact.messageLabel}</label>
              <textarea
                id="message"
                name="message"
                placeholder={t.contact.messagePlaceholder}
                value={message}
                onChange={(e) => {
                  setMessage(e.target.value);
                }}
                style={{ borderImageSource: `url(${border_dark})` }}
              ></textarea>
            </div>
            <button className="contact-submit-button retro-button primary" type="submit">
              ✉️ {t.contact.sendButton}
            </button>
          </form>
        </>
      );
    }
  };
  const guestbookCount = Math.floor(Math.random() * 9999) + 1000;

  return (
    <div id="contact" className="retro-page">
      <PageNavigation currentSection="goTo-contact" onNavigate={setCurrentAction} lang={lang} />

      <div className="page-content">
        <h1 className="contact-title">{t.contact.title}</h1>

        <div className="contact-form-container retro-box">{renderForm()}</div>

        <div
          className="visitor-counter"
          style={{ margin: '20px auto', textAlign: 'center', display: 'table' }}
        >
          {interpolate(t.contact.visitorsSigned, { count: guestbookCount })}
        </div>
      </div>
    </div>
  );
};

export default Contact;
